// https://observablehq.com/@bbjacob123/rt-chart@1362
export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer()).define(["md"], function(md){return(
md`# Rt Chart

Gráfico do número de reprodução do coronavirus no tempo por estado do Brasil.`
)});
  main.variable(observer("chart")).define("chart", ["d3","width","height","x","data","y","negativeColor","positiveColor","yAxis"], function(d3,width,height,x,data,y,negativeColor,positiveColor,yAxis)
{
  const svg = d3.create("svg")
      .attr("viewBox", [0, 0, width, height]);
  
  const bandajdust = 0.7
  var font = x.bandwidth()*0.36

  
//    var tooltip = svg.append("g")
//       .attr("class", "tooltip")
//       .style("display", "none");
  
  
//   tooltip.append("text")
//      .attr("x", 15)
//      .attr("dy", "1.2em")
//      .style("font-size", "1.25em")
//      .attr("font-weight", "bold");
  
  
  const bars = svg.append("g")
      //.attr("fill", color)
      .attr("fill-opacity", 0.1)
    .selectAll("rect") // change all rectangles
    .data(data)
    .join("rect")
      .attr("x", (d, i) => x(i)) // d, i means is an element from the dataset array and its index
      .attr("y", d => y(d.upper_bound)) // upper bound for all the bar charts is the upper bound for each data element
      .attr("ry",x.bandwidth()*bandajdust/2) // changes radius of tip for bar charts
      .attr("rx",x.bandwidth()*bandajdust/2) // not sure
      .attr("height", d => y(d.lower_bound)-y(d.upper_bound)) // height of bars
      .attr("width", x.bandwidth()*bandajdust*0.53) // width of rectangles
      .attr("fill", d => (d.ml > 1) ? negativeColor : positiveColor) // fill bar condition based on d.ml value 
//       .on("mouseover", function ()
               
//           {
//         tooltip.style("display", null);
//       })
//       .on("mouseout", function ()

//       {
//     tooltip.style("display", "none");
//   })
//       .on("mousemove", function (d)

//       {
//     var xPos = d3.mouse(this)[0] - 15;
//     var yPos = d3.mouse(this)[1] - 55;
//     tooltip.attr("transform", "translate(" + xPos + "," + yPos + ")");
//     tooltip.select("text").text("Rt: " + d.ml);
//   });
  
  
  
 const nodes = svg.append("g")
           .selectAll("circle")
           .data(data)
           .enter()
           // .join("circle")
           // Add one g element for each data node here.
           .append("g")
           .attr("class", "nodes")
           // .join("circle")
           .attr("transform", function(d, i) {
             return "translate(" + (x(i) + (x.bandwidth()*bandajdust*0.57)/2) + "," + y(d.ml) + ")"; 
           }) // nodes placement


nodes.append("circle")
      .attr("class", "node")
      .attr("fill", "white") // color
      .attr("stroke", d => (d.ml > 1) ? negativeColor : positiveColor) // stroke line color
      .attr("r", x.bandwidth()*bandajdust/1.9) // circle radius
      .on("mousemove click touchmove", function() { // add animation where opacity increases with mouse on top and goes back to normal opacity 
        d3.select(this).attr("r", x.bandwidth()*bandajdust/1.7)
      })
      .on("mouseout", function() {
        d3.select(this).attr("r", x.bandwidth()*bandajdust/1.9)
      })
  


// Add a text element to the previously added g element.
nodes.append("text")
     .attr("text-anchor", "start")
     .style("font", font+"px sans-serif")
     .attr("transform", `translate(${x.bandwidth()*-0.27}, ${x.bandwidth()*0.13})`)
     .attr("fill", d => (d.ml > 1) ? negativeColor: positiveColor)
     .attr("class", "node-text") // for rotation
     .text(d => d.state);
  
  
  svg.append("g")
      .call(yAxis)
      .selectAll("text")
        .attr("class", "axis-label");
  
  return svg.node();
}
);
  main.variable(observer("positiveColor")).define("positiveColor", function(){return(
"green"
)});
  main.variable(observer("negativeColor")).define("negativeColor", function(){return(
"darkred"
)});
  main.variable(observer("x")).define("x", ["d3","data","margin","width"], function(d3,data,margin,width){return(
d3.scaleBand()
    .domain(d3.range(data.length))
    .range([margin.left, width - margin.right])
    .padding(0.35)
)});
  main.variable(observer("y")).define("y", ["d3","data","height","margin"], function(d3,data,height,margin){return(
d3.scaleLinear()
    .domain([0, d3.max(data, d => d.upper_bound)]).nice()
    .range([height - margin.bottom, margin.top])
)});
  main.variable(observer("xAxis")).define("xAxis", ["height","margin","d3","x","data"], function(height,margin,d3,x,data){return(
g => g
    .attr("transform", `translate(0,${height - margin.bottom})`)
    .call(d3.axisBottom(x).tickFormat(i => data[i].state).tickSizeOuter(0))
)});
  main.variable(observer("yAxis")).define("yAxis", ["margin","d3","y","x","width"], function(margin,d3,y,x,width){return(
g => g
  .attr("transform", `translate(${margin.left-8},0)`)
  .call(d3.axisLeft(y)
      .tickValues(d3.scaleLinear().domain(y.domain()).ticks())
      .tickSize(0)
   )
  .call(g => g.selectAll(".tick line") // horizontal line
    .select(function() { return this.parentNode.appendChild(this.cloneNode()); })
      .attr("stroke-opacity", d => d === 1 ? null : 0.1) // if value is 1 opacity is different
      .attr("stroke-width", x.bandwidth()*0.06) // width of grid
      .attr("stroke", 'darkgray')
      .attr("x2", width - margin.left - margin.right))
  .call(g => g.select(".domain").remove())
)});
  main.variable(observer("color")).define("color", function(){return(
"#ccc"
)});
  main.variable(observer("height")).define("height", function(){return(
500
)});
  main.variable(observer("margin")).define("margin", function(){return(
{top: 30, right: 20, bottom: 30, left: 40}
)});
  main.variable(observer("data_raw")).define("data_raw", ["d3"], function(d3){return(
Object.assign(d3.csv("https://hackcovid.s3-us-west-2.amazonaws.com/data/rt_data.csv", d3.autoType), {format: "%", y: "↑ Frequency"})
)});
  main.variable(observer("data_without_br")).define("data_without_br", ["data_raw"], function(data_raw){return(
data_raw.filter(d => d.state !== "Brasil")
)});
  main.variable(observer("lastDate")).define("lastDate", ["d3","data_without_br"], function(d3,data_without_br){return(
d3.max(data_without_br, d => d.date)
)});
  main.variable(observer("data_filtered")).define("data_filtered", ["data_without_br","lastDate"], function(data_without_br,lastDate){return(
data_without_br.filter(d => d.date.toString() === lastDate.toString())
)});
  main.variable(observer("data")).define("data", ["data_filtered","d3"], function(data_filtered,d3){return(
data_filtered.sort((x,y) => d3.ascending(x.ml, y.ml))
)});
  main.variable(observer("d3")).define("d3", ["require"], function(require){return(
require("d3@5")
)});
  return main;
}
